<script setup>
  import Contact from '@/components/Contact.vue';
  import AboutMe from '@/components/AboutMe.vue';


  let aboutLink = document.getElementById("about_link")
  let skillsLink = document.getElementById("skills_link")
  let realLink = document.getElementById("real_link")
  let contactLink = document.getElementById("contact_link")

  function activeLink(e){
    e.style.color='#A0006D'
    e.style.textDecoration="none"
    e.style.fontWeight=700
  }

  function removeLink(e){
    e.style.textDecoration="none"
    e.style.fontWeight=400
    e.style.color="#060640"
  }
</script>

<template>
    <AboutMe @pointerenter="activeLink(aboutLink)" @pointerleave="removeLink(aboutLink)"/>

    <skills @pointerenter="activeLink(skillsLink)" @pointerleave="removeLink(skillsLink)"/>

    <Realisations @pointerenter="activeLink(realLink)" @pointerleave="removeLink(realLink)"/>

    <Contact @pointerenter="activeLink(contactLink)" @pointerleave="removeLink(contactLink)"/>
</template>

<style scoped>

</style>